import React, { Fragment, useEffect } from 'react';
import * as d3 from 'd3';
import { Card, Content } from '../../components/Fule';
// import { text } from 'd3';
// 
const title = '';
const data = [
  { name: '', number: 18 },
  { name: '', number: 120 },
  { name: '', number: 9 },
  { name: '', number: 1 },
  { name: '', number: 9 },
  { name: '', number: 6 },
  { name: '', number: 3 },
  { name: '', number: 4 },
  { name: '', number: 10 },
  { name: '', number: 5 },
  { name: '', number: 12 },
  { name: '', number: 16 },
  { name: '', number: 9 },
];
export default function PluginsD3() {
  // const anchoRef = useRef(null);
  // 
  const width = 800; // 
  const height = 400; // 
  const MaxValue =
    d3.max(data, (d) => d.number) > 110 ? d3.max(data, (d) => d.number) : 110; // 
  const barColor = '#276419'; // 
  const margin = { top: 80, right: 20, bottom: 40, left: 30 }; // 
  // eslint-disable-next-line react-hooks/exhaustive-deps
  const parameter = { title, data, width, height, barColor, margin, MaxValue };

  //  -  bar Chart
  const DrawChart = (parameter) => {
    // 
    const {
      title = '',
      width = 200,
      height = 200,
      barColor = '#ccc',
      margin = { top: 40, right: 20, bottom: 40, left: 30 },
      MaxValue = d3.max(data, (d) => d.number),
    } = parameter;
    const svg = d3
      .select('#svgD3')
      .append('svg')
      .attr('width', width)
      .attr('height', height);
    const innerWidth = width - margin.left - margin.right;
    const innerHeight = height - margin.top - margin.bottom;

    // x
    const xScale = d3
      .scaleBand() //  scaleBandbarbarpadding
      .domain(data.map((d) => d.name)) // ydata.map
      .range([0, innerWidth]) // 
      .paddingInner(0) // 
      .paddingOuter(0.2); // 
    // y
    const yScale = d3
      .scaleLinear()
      .domain([MaxValue, 0]) // d=>d.number
      .range([0, innerHeight]); // 

    // 
    const barLine = svg
      .append('g')
      .attr('class', 'barLine')
      .attr('x', margin.left)
      .attr('y', margin.top);

    // 
    const bar = svg.append('g').attr('class', 'bar');
    // X 
    const axisX = svg.append('g').attr('class', 'axisBottom');
    // Y 
    const axisY = svg.append('g').attr('class', 'axisLeft');

    // 
    barLine
      .append('rect')
      .attr('width', innerWidth)
      .attr('height', innerHeight)
      .attr('x', margin.left)
      .attr('y', margin.top)
      .attr('fill', '#ccc')
      .attr('opacity', '0.2');
    // 
    data.forEach((d, i) => {
      const item = bar.append('g');
      // 
      // barLine
      //   .append('line')
      //   .attr('x1',margin.left)
      //   .attr('y1',margin.top + i * innerHeight/data.length)
      //   .attr('x2',margin.left + innerWidth)
      //   .attr('y2',margin.top + i * innerHeight/data.length)
      //   .attr("stroke", "#ccc")
      //   .attr("stroke-width", "1px");
      // 
      // item
      //   .append("rect")
      //   .attr("width", xScale.bandwidth() / 2) //
      //   .attr("fill", barColor)
      //   .attr("x", xScale(d.name) + margin.left + xScale.bandwidth() / 4)
      //   .attr("y", yScale(d.number) + margin.top)
      //   .attr("transform", `rotate(-90)`)
      //   .transition()
      //   .duration(1000)
      //   .delay((d, i)=>{
      //     return i * 200;
      //   })
      //   .attr("height", innerHeight - yScale(d.number))
      //   .attr("height", 0)
      item
        .append('polygon')
        .attr('fill', barColor)
        .attr(
          'points',
          `
          ${xScale(d.name) + margin.left + xScale.bandwidth() / 4},${
            yScale(d.number) + margin.top + innerHeight - yScale(d.number)
          } 
          ${
            xScale(d.name) +
            margin.left +
            xScale.bandwidth() / 4 +
            xScale.bandwidth() / 2
          },${yScale(d.number) + margin.top + innerHeight - yScale(d.number)}
          ${
            xScale(d.name) +
            margin.left +
            xScale.bandwidth() / 4 +
            xScale.bandwidth() / 2
          },${yScale(d.number) + margin.top + innerHeight - yScale(d.number)}
          ${xScale(d.name) + margin.left + xScale.bandwidth() / 4},${
            yScale(d.number) + margin.top + innerHeight - yScale(d.number)
          }
        `
        )
        .transition()
        .duration(1000)
        .delay((d, i) => i * 200)
        .attr(
          'points',
          `
          ${xScale(d.name) + margin.left + xScale.bandwidth() / 4},${
            yScale(d.number) + margin.top
          } 
          ${
            xScale(d.name) +
            margin.left +
            xScale.bandwidth() / 4 +
            xScale.bandwidth() / 2
          },${yScale(d.number) + margin.top}
          ${
            xScale(d.name) +
            margin.left +
            xScale.bandwidth() / 4 +
            xScale.bandwidth() / 2
          },${yScale(d.number) + margin.top + innerHeight - yScale(d.number)}
          ${xScale(d.name) + margin.left + xScale.bandwidth() / 4},${
            yScale(d.number) + margin.top + innerHeight - yScale(d.number)
          }
        `
        );
      // 
      item
        .append('text')
        .text(d.number)
        .attr('x', xScale(d.name) + margin.left + xScale.bandwidth() / 4)
        .attr('y', yScale(d.number) + margin.top - 6)
        .attr('fill', '#333')
        .style('font-size', '12px')
        .attr('text-anchor', 'middle')
        .attr('dx', xScale.bandwidth() / 4);
    });
    //  X 
    axisX
      .call(d3.axisBottom().scale(xScale))
      .attr(
        'transform',
        `translate(${margin.left},${innerHeight + margin.top})`
      )
      // 
      .selectAll('text')
      .style('text-anchor', 'end')
      .attr('dx', '-.8em')
      .attr('dy', '.15em')
      .attr('transform', 'rotate(-50)');

    //  Y 
    axisY
      .attr('class', 'axisLeft')
      .call(d3.axisLeft().scale(yScale))
      .attr('transform', `translate(30, ${margin.top})`);

    // 
    svg
      .append('g')
      .attr('class', 'title')
      .append('text')
      .text(title)
      .attr('x', width / 2)
      .attr('y', 14)
      .attr('text-anchor', 'middle')
      .attr('fill', '#333')
      .style('font-size', '16px');

    // 
    svg
      .append('g')
      .attr('class', 'subtitle')
      .append('text')
      .text(title)
      .attr('x', width / 2)
      .attr('y', 38)
      .attr('text-anchor', 'middle')
      .attr('fill', '#333')
      .style('font-size', '12px');
  };

  // 
  useEffect(() => {
    DrawChart(parameter);
  }, [parameter]);
  return (
    <Fragment>
      <Content>
        <Content.Header>D3</Content.Header>
        <Content.Body>
          <Card>
            <Card.Header> (bar chart)</Card.Header>
            <Card.Body>
              <div id="svgD3"></div>
            </Card.Body>
          </Card>
        </Content.Body>
      </Content>
    </Fragment>
  );
}
