import React, { useContext } from 'react';
import classNames from 'classnames';
import { Variable, PublicContent } from '../Utility/Utility';
import './index.scss';

export function Col(props) {
  const gutter = useContext(PublicContent);
  const {
    className, // 
    style, // 
    width, // 
    pull, //  
    push, //  
    colSpan, // 2424
    phone, // 
    ...rest // 
  } = props;
  const cls = classNames(
    `${Variable}-col`,
    {
      [`${Variable}-col-${colSpan}`]: colSpan,
      [`${Variable}-col--push-${push}`]: push,
      [`${Variable}-col--pull-${pull}`]: pull,
      [`${Variable}-col--phone-${phone}`]: phone,
    },
    className
  );
  const styles = {
    ...style,
    width: `${width}px`,
  };

  if (gutter) {
    const x = gutter[0] || 0;
    const y = gutter[1] || 0;
    styles.padding = `${y / 2}px ${x / 2}px`;
  }

  return (
    <div className={cls} style={styles} {...rest}>
      {props.children}
    </div>
  );
}
